.message {
    width: 80%;
    margin: 1rem auto;
    display: flex;

  }
  .message_input {
    width: 100%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
  }

  .submitbutton{
    padding: 1rem;
    
    background: #5a5959;
    color:#fff;
    font-size:1rem;
    outline: 0;
    border: 0;
    border-radius: 10px;
    margin:0.5rem;
    
  }

  .submitbutton:hover{
    color:rgba(196, 196, 196, 0.733);
    background: rgba(0,0,0,0.6);
 
  }