@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
#root{
  height: 100vh;
}



body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-family: "Nunito Sans", sans-serif;
}





.sidebar{
  width: 80px;
  background-color: rgba(32, 32, 35, 1);
  height: 100%;
  border-right: 1px solid rgba(189, 189, 192, 0.1);
  transition: width 0.3s;
  overflow-y: scroll;
 

}

@media (min-width: 576px) {
    .sidebar {
      width: 320px;
    }
  }
.user {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding: 1rem;
}
.user:hover {
  background: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.userPic {
  width: 100px;
  border-radius: 50%;
}
.user_details {
  display: none;
}

/* not small devices  */
@media (min-width: 576px) {
  .user_details {
    display: block;
    padding: 0 0 0 1rem;
  }
  .user-name {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-size: 1rem;
  }
}
.Empty {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100%;
  }

  .Empty__name {
    color: #fff;
  }
  .Empty__status,
  .Empty__info {
    padding: 1rem;
  }
  .Empty__status {
    color: rgba(255, 255, 255, 0.9);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  }
  .Empty__img {
    border-radius: 50%;
    margin: 2rem 0;
  }
  .Empty__btn {
    padding: 1rem;
    margin: 1rem 0;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 30px;
    outline: 0;
  }
  .Empty__btn:hover {
    background: rgba(255, 255, 255, 0.7);
    cursor: pointer;
  }
.main {
    -webkit-flex: 1 1;
            flex: 1 1;
    background-color: rgba(25, 25, 27, 1);
    height: 100%;
  }
.chatwindow {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
}
.header {
    padding: 1rem 1rem;
    border-bottom: 1px solid rgba(189, 189, 192, 0.2);
  }
  .name {
    color: #fff;
  }

  .Header {
    padding: 1rem 2rem;
    border-bottom: 1px solid rgba(189, 189, 192, 0.2);
  }

.chats{
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    width: 85%;
    margin: 0 auto;
    overflow-y: scroll;
}

.left{
    margin: 1rem 0;
  color: #fff;
  padding: 1rem;
  background: linear-gradient(90deg, #1986d8, #7b9cc2);
  max-width: 90%;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.right{
  margin-left: auto;
  background: #2b2c33;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding: 1rem;
}


.message {
    width: 80%;
    margin: 1rem auto;
    display: -webkit-flex;
    display: flex;

  }
  .message_input {
    width: 100%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
  }

  .submitbutton{
    padding: 1rem;
    
    background: #5a5959;
    color:#fff;
    font-size:1rem;
    outline: 0;
    border: 0;
    border-radius: 10px;
    margin:0.5rem;
    
  }

  .submitbutton:hover{
    color:rgba(196, 196, 196, 0.733);
    background: rgba(0,0,0,0.6);
 
  }
.container {
  display:-webkit-flex;
  display:flex;
  height: 100%;
  color: rgba(189, 189, 192, 1);
}



